import {TemplateRef} from '@angular/core';
import {Edge as FlowEdge, Node as FlowNode} from '@swimlane/ngx-graph';
import {AdaptChartColorStatus} from '../services/chart-color.service';

export enum AdaptFlowChartOrientation {
  LEFT_TO_RIGHT = 'LR',
  RIGHT_TO_LEFT = 'RL',
  TOP_TO_BOTTOM = 'TB',
  BOTTOM_TO_TOP = 'BT'
}

export interface AdaptFlowChartNode extends FlowNode {
  data?: {
    text: string;
    icon?: string;
    status?: AdaptChartColorStatus;
    iconCustomClass?: string;
  }[];
  description?: string;
  icon?: string;
  status?: AdaptChartColorStatus;
  type?: string;
  iconCustomClass?: string;
}

export interface AdaptFlowChartEdge extends FlowEdge {
  type?: string;
}

export interface AdaptFlowChartTexts {
  fitGraphButton: string;
}

export interface AdaptFlowChartEmptyStateConfig {
  label?: string;
  inverted?: boolean;
}

export interface AdaptFlowChartCustomTemplate {
  /**
   * Template for node
   * @since v12.24.0
   */
  nodeTemplate?: TemplateRef<{$implicit: AdaptFlowChartNode}>;

  /**
   * Template for defs
   * @since v12.24.0
   */
  defsTemplate?: TemplateRef<void>;

  /**
   * Template for link
   * @since v12.24.0
   */
  linkTemplate?: TemplateRef<{$implicit: AdaptFlowChartEdge}>;
}
